@font-face {
	font-family: 'scriptinaRegular';
	src: url('../../fonts/scriptinaRegular.eot');
	src: local('☺'), url('../../fonts/scriptinaRegular.woff') format('woff'), url('../../fonts/scriptinaRegular.ttf') format('truetype'), url('../../fonts/scriptinaRegular.svg') format('svg');
	font-weight: 400;
	font-style: normal;
}
@font-face {
	font-family: 'proximanovaRegular';
	src: url('../../fonts/proximanovaRegular.eot');
	src: local('☺'), url('../../fonts/proximanovaRegular.woff') format('woff'), url('../../fonts/proximanovaRegular.ttf') format('truetype'), url('../../fonts/proximanovaRegular.svg') format('svg');
	font-weight: 400;
	font-style: normal;
}

.container {
    max-width: 1260px;
    margin: 0 auto;
    padding: 0 15px;
}
.wrapper {
    overflow: hidden;
}
.item-title {
    color: #56534c;
    font-family: 'Alegreya', serif;
    font-weight: bold;
    font-size: 3rem;
}
.text-title {
    color: #aa8669;
    font-size: 0.79rem;
    text-transform: uppercase;
    margin-top: 0.6em;
}
.text {
    font-family: 'proximanovaRegular';
    font-size: 1rem;
    color: #56534c;
    margin-top: 4.36em;
    line-height: 1.5;
}
.fixed {
    position: fixed;
    width: 100%;
    z-index: 1000;
}
.main-nav-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .main-nav-list-item {
        margin-left: 2.57em;
        padding-right: 3.43em;
        position: relative;
        a {
            color: #ffffff;
            font-family: 'Alegreya', serif;
            font-size: 1rem;
            text-transform: uppercase;
            letter-spacing: 0.3em;
            display: inline-block;
            transition: all 0.2s ease;
            &:hover {
                opacity: 0.6;
            }
        }
        &::after {
            content:'~';
            display: inline-block;
            color: #ffffff;
            position: absolute;
            right: 0;
            top: 0;
        }
        &:first-child {
            margin-left: 0;
        }
        &:last-child {
            padding-right: 0;
            &::after {
                display: none;
            }
        }
    }
}
.main-logo {
    a {
        font-family: 'scriptinaRegular', sans-serif;
        color: #ffffff;
        font-size: 2.14rem;
        letter-spacing: 0.3em;
    }
}
header {
    &.main-header {
        overflow: hidden;
        position: relative;
        height: 100vh;
        background: url(../../build/img/bg.jpg) no-repeat center;
        background-size: cover;
        padding-bottom: 4em;
        .header-nav {
            background-color: rgba(86, 83, 76, 0.529);
            padding: 1.5em 0;
            width: 100%;
            position: absolute;
            z-index: 1000;
            transform: translate(0, 0);
            .container {
                nav {
                    &.main-nav {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        position: relative;
                    }
                }
            }
            &.fixed {
                position: fixed;
                width: 100%;
                z-index: 1000;
                animation: opacity 0.3s linear;
                @keyframes opacity {
                    0% {
                        transform: translateY(-50px);
                    }
                    100% {
                        transform: translateY(0px);
                    }
                }
            }
          
        }
        .promo {
            max-width: 776px;
            margin: 0 auto;
            text-align: center;
            position: relative;
            z-index: 13;
            padding-bottom: 15.36em;
            &::after {
                content: '';
                width: 100%;
                height: 100%;
                bottom: 0;
                background: radial-gradient(circle, rgba(86, 83, 76,0.829) 0%, rgba(86, 83, 76,0) 60%);
                position: absolute;
                top: 0;
                left: 0;
                z-index: -1;
                border-radius: 50%;
            }
            .main-title {
                margin-top: 28.39vh;
                font-size: 6.57rem;
                color: #ffffff;
                font-family: 'Playfair Display', serif;
                padding-bottom: 0.77em;
                position: relative;
                display: inline-block;
                margin-bottom: 0.5em;
                &::after {
                    content: '';
                    width: 30%;
                    background: url(../../build/img/dashedline.png) no-repeat center;;
                    height: 15px;
                    display: block;
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                } 
            }
            .promo-text {
                max-width: 586px;
                margin: 0 auto;
                text-shadow: 0px 1px 0px rgba(61, 48, 41, 0.004);
                font-family: 'Alegreya', serif;
                font-size: 1.71rem;
                line-height: 1.5;
                color: #ffffff;
                letter-spacing: 0.1em;
            }
            .promo-link {
                margin-top: 4.5em;
                display: inline-block;
                font-family: 'Alegreya', serif;
                font-size: 1rem;
                padding: 1.5em 3.64em;
                color: #ffffff;
                border-width: 1px;
                border-color: rgb(158, 156, 147);
                border-style: dashed;
                background-color: rgba(86, 83, 76, 0.529);
                text-transform: uppercase;
                letter-spacing: 0.1em;
                transition: all 0.5s ease;
                &:hover {
                    transform: scale(1.2);
                }
            }
        }
        .mobile-menu-list {
            display: none;
            top: 0;
            bottom: 0;
            width: 100%;
            z-index: 99;
            padding-top: 75px;
        }
    }
}
main {
    .art {
        padding: 7.64em 0;
        padding-top: 12.64em;
        background: url(../../build/img/art.jpg) no-repeat center;
        background-size: cover;
        .container {
            .row {
                display: flex;
                justify-content: space-between;
                
                .text-item {
                    flex: 0 1 50%;
                    max-width: 50%;
                    text-align: center;
                    max-width: 480px;
                    margin: 0 auto;
                  
                  
                    .chef {
                        font-family: 'Alegreya', serif;
                        font-weight: 700;
                        color: #56534c;
                        font-size: 1.71rem;
                        margin-top: 1.625em;
                    }
                    .img-chef {
                        margin-top: 3.36em;
                        display: inline-flex;
                        justify-content: space-between;
                        align-items: center;
                        .name {
                            font-family: 'Alegreya', serif;
                            font-weight: 400;
                            color: #56534c;
                            font-size: 1.71em;
                            padding: 0.7em 0;
                            border-top: 1px solid #d7d5ca;
                            border-bottom: 1px solid #d7d5ca;
                        }
                        .imgchef {
                            margin: 0 2.14em;
                        }
                    }
                    .quote {
                        margin: 0 auto;
                        margin-top: 3.14em;
                        max-width: 236px;
                        .quote-text {
                            font-family: 'scriptinaRegular', sans-serif;
                            color: #a47d5e;
                            font-size: 1.71rem;
                            line-height: 1.3;
                            position: relative;
                            &::before,
                            &::after {
                                color: #56534c;
                                opacity: 0.5;
                                font-size: 4rem;
                                position: absolute;
                                font-family: 'Times New Roman';
                                line-height: 0;
                            }
                            &::before {
                                content: '“';
                                left: -20%;
                                top: 0;
                            }
                            &::after {
                                content: '”';
                                right: -20%;
                                bottom: 0;
                                transform: matrix( 1,0,0,-1,0,0);
                            }
                        }
                    }
                }
                .img-item {
                    height: 100%;;
                    flex: 0 1 50%;
                    max-width: 50%;
                    position: relative;
                    
                    .row {
                        height: 100%;
                        display: flex;
                        flex-wrap: wrap;
                        margin: 0 -9px;
                        margin-bottom: -18px;
                     
                        .column {
                            flex: 0 1 50%;
                            max-width: 50%;
                            padding: 0 9px;
                            height: 100%;
                            margin-bottom: 18px;
                            .img-box {
                                width: 100%;
                                outline: 5px solid #ffffff;
                                outline-offset: -11px;
                                img {
                                    width: 100%;
                                    
                                }
                            }
                        }
                    }
                    .imgitemabove {
                        position: absolute;
                        text-align: center;
                        color: #c9b176;
                        font-size: 2.14em;
                        font-family: 'Alegreya', serif;
                        font-weight: 400;
                        background: url(../../build/img/imgitembg.png) no-repeat center;
                        display: inline-block;
                        padding: 4.7em 1.67em;
                        max-width: 253px; 
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                    }
                }
            }
        }
    }
    .recipes {
        background: url(../../build/img/recbg.jpg) no-repeat center;
        background-size: cover;
        padding-top: 10.21em;
        padding-bottom: 7.43em;
        .container {
            .recipes-body {
               background-color: #ffffff;
               position: relative;
               max-width: 960px;
               margin: 0 auto;
               display: flex;
             
               .img-item {
                   flex: 0 1 44%;
                   max-width: 44%;
                 
                img {
                    width: 100%;
                    height: 100%;
                }
               }
               .text-item {
                   background: url(../../build/img/windmill.png) no-repeat right bottom;
                   padding-top: 5.21em;
                   padding-bottom: 2.14em; 
                   flex: 0 1 56%;
                   max-width: 56%;
                   .text-item-body {
                       max-width: 400px;
                       margin: 0 auto;
                       .text {
                           font-family: 'proximanovaRegular';
                           font-size: 1rem;
                           color: #56534c;
                           margin-top: 4.36em;
                           line-height: 1.5;
                       }
                   }
                   .slider {
                       margin-top: 6.93em;
                       position: relative;
                       max-width: 400px;
                       margin-left: auto;
                       margin-right: auto;
                       .slick-list {
                           padding: 0 15px;
                       }
                       .slick-track {
                           display: flex;
                           align-items: center;
                           .slick-slide {
                               img {
                                width: auto;
                                margin: 0 auto;
                               }
                           }
                       }
                       .slick-arrow {
                           position: absolute;
                           font-size: 0;
                           top: calc(50% - 16px);
                           z-index: 10;
                           background: url(../../build/img/slide-arrow.png) no-repeat center;
                           width: 32px;
                           height: 32px;
                           &.slick-prev {
                               left: -10px;
                               transform: rotate(180deg);
                           }
                           &.slick-next {
                               right: -15px;
                           }
                           &.slick-disabled {
                               opacity: 0.5;
                           }
                       }
                   }
               }
               .time-box {
                   position: absolute;
                   right: -40px;
                   top: -40px;
                   padding-right: 2em;
                   .text-body {
                       text-align: center;
                       box-shadow: -10px 9px 32px -2px rgba(163,163,163,0.77);
                       background: url(../../build/img/recafterbg.png) no-repeat center;
                       padding: 2.29em 3.71em;
                       .ready {
                           color: #aa8669;
                           font-family: 'Playfair Display', serif;
                           text-transform: uppercase;
                           font-size: 1.5rem;
                       }
                       .time {
                           color: #dfc691;
                           font-family: 'Alegreya', serif;
                           font-weight: bold;
                           font-size: 4rem;
                       }
                       .minutes {
                           color: #9e998d;
                           margin-top: 1em;
                           font-size: 1rem;
                           font-family: 'Playfair Display', serif; 
                       }
                   }
               }
            }
        }
    }
    .breakfast {
        background: url(../../build/img/breakfast-bg.jpg) no-repeat center;
        background-size: cover;
        padding-top: 6.43em;
        padding-bottom: 4.5em;
        .container {
            display: flex;
            align-items: center;
            .timetable {
                flex: 0 1 50%;
                max-width: 50%;
                text-align: center;
                margin-left: 5em;
                .item-title {
                    color: #ffffff;
                }
                ul {
                    li {
                        padding-top: 3.64em;
                        background: url(../../build/img/libg.png) no-repeat center top;
                        margin-bottom: 3em;
                        &:first-child {
                            background: none;
                        }
                        .itm-name {
                            color: #ffffff;
                            font-size: 1.5rem;
                            font-family: 'Alegreya', serif;
                            font-weight: bold;
                        }
                        .item-description {
                            font-family: 'proximanovaRegular';
                            font-size: 1rem;
                            color: #c2beba;
                            margin-top: 0.75em
                        }
                        .item-price {
                            font-family: 'Alegreya', serif;
                            font-size: 2.57rem;
                            color: #dfc691;
                            margin-top: 0.75em
                        }
                    }
                }
            }
            .flair {
                flex: 0 1 50%;
                max-width: 50%;
                padding-right: 3em;
                .flair-body {
                   padding: 3.43em 2.5em 5.71em 2.5em;
                   box-shadow: 32px 0px 0px -16px #d8d4c6,
                   64px 0px 0px -32px #c1beb3;
                    text-align: center;
                    max-width: 445px;
                    margin: 0 auto;
                    background: url(../../build/img/psh.png) no-repeat right bottom,
                    url(../../build/img/br-bg.png) no-repeat left top;
                    background-size: 60%, cover;
                    .top {
                        display: flex;
                        justify-content: space-around;
                        p {
                            font-size: 1rem;
                            font-family: 'Alegreya', serif;
                            color: #56534c;
                            font-weight: 700;
                        }
                    }
                    .line {
                        margin-top: 1em;
                        margin-bottom: 5em;
                        img {
                            max-width: 100%;

                        }
                    }
                    .main-text {
                        .item-title {
                            line-height: 1.36;
                        }
                    }
                    .btn {
                        margin-top: 3.5em;
                        a {
                            display: inline-block;
                            padding: 1.06em 3.89em; 
                            font-size: 1.29em;
                            text-transform: uppercase;
                            color: #a47d5e;
                            font-family: 'Alegreya', serif;
                            font-weight: 700;
                            border: 1px solid #9e9c93;
                            transition: all 0.5s ease;
                            &:hover {
                                transform: scale(1.2);
                            }
                        }
                    }
                }
            }
        }
    }
    .sandwich {
        background: url(../../build/img/sand-bg.png) no-repeat center top;
        background-color: #f3f1e9;
        padding-bottom: 4em;
        .item-title,
        .text-title {
            text-align: center;
        }
        .item-title {
            padding-top: 2.5em;
        }
        .container {
            margin-top: 2em;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .list {
                flex: 0 1 23.49%;
                text-align: center;
                max-width: 23.49%;
                ul {
                    li {
                        margin-top: 3.5em;
                        .img-item {
                            margin-bottom: 1em;
                            img {
                                box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.2);
                                border-radius: 50%;
                            }
                        }
                        .item-name {
                            color: #a47d5e;
                            font-size: 2.14rem;
                            font-family: 'Alegreya', serif;
                            font-weight: 700;
                        }
                        .text {
                            margin-top: 1.85em;
                        }
                    }
                }
            }
            .sand {
                flex: 0 1 66.9%;
                max-width: 66.9%;
                position: relative;   
                img {
                    display: block;
                }
                .sand-after {
                    position: absolute;
                    top: -5px;
                    right: 0;
                    transform: translateY(-2%);
                    ul {
                        text-align: center;
                        li {
                            padding: 3.21em 4.43em;
                            background-color: rgba(255, 255, 255, 0.8);
                            .text-body {
                                .ready {
                                    color: #a47d5e;
                                    font-family: 'Playfair Display', serif;
                                    text-transform: uppercase;
                                    font-size: 1.3rem;
                                }
                                .time {
                                    color: #56534c;
                                    font-family: 'Alegreya', serif;
                                    font-weight: bold;
                                    font-size: 4rem;
                                }
                                .minutes {
                                    color: #a39d91;
                                    margin-top: 1em;
                                    font-size: 1rem;
                                    font-family: 'Playfair Display', serif; 
                                }
                            }
                            &:last-child {
                                background: url(../../build/img/last-libg.png) no-repeat center;
                                background-size: cover;
                                .ready {
                                    color: #a47d5e;
                                    font-family: 'Playfair Display', serif;
                                    text-transform: uppercase;
                                    font-size: 1.3rem;
                                }
                                .time {
                                    color: #dfc691;
                                    font-family: 'Alegreya', serif;
                                    font-weight: bold;
                                    font-size: 5rem;
                                }
                            }
                        }
                    }
                }
                .sand-before {
                    position: absolute;
                    left: -20px;
                    bottom: -10px;
                    a {
                        display: inline-block;
                        color: #dfc691;
                        border: 1px dashed #dfc691;
                        background: #ffffff;
                        padding: 1.29em 2em;
                        text-transform: uppercase;
                        font-family: 'Alegreya', serif;
                        font-weight: 700;
                        transition: all 0.5s ease;
                            &:hover {
                                transform: scale(1.2);
                            }
                    }
                }
            }
        }
    }
}
footer {
    padding: 1em 0;
    background-color: #f3f1e9;
    overflow: hidden;
    .container {
        .footer-body {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .footer-menu,
            .footer-events {
                border-top: 1px solid #c2beba;
                border-bottom: 1px solid #c2beba;
                padding: 1.5em 0;
                .main-nav-list {
                    display: block;
                    li {
                        a {
                            color: #56534c;
                        }
                        &:after {
                            color: #56534c;
                        }
                    }
                }
                .main-nav-list {
                    display: flex;
                    justify-content: space-between;
                }
            }
            .main-logo {
                a {
                    color: #56534c;
                    line-height: normal;
                }
            }
        }
    }
}


